import React from "react";
import { connect } from "react-redux";
// nodejs library that concatenates classes
import { Link } from "react-router-dom";
import classnames from "classnames";
import './Login.scss';
// reactstrap components

import {
  Button,
  Card,
  CardBody,
  FormGroup,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Container,
  Row,
  Col
} from "reactstrap";
// core components

import { AUTH_ACTIONS } from "redux/actions";
import AuthHeader from "components/Headers/AuthHeader.jsx";

class Login extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
      password: '',
      errors: null
    }
  }

  handleChange = e => {
    const { name, value } = e.target;
    this.setState(
      { [name]: value }
    );
  }

  login = e => {
    e.preventDefault();
    const { email, password } = this.state;
    const type = this.getPortalType();
    if (email && password) {
      this.props.login({ email, password, type });
    }
  }

  getConfigObject = pathName => {
    switch (pathName) {
      case '/admin/login':
        return {
          registerPath: '/admin/register',
          headerTitle: 'Admin'
        }
      case '/provider/login':
        return {
          registerPath: '/provider/register',
          headerTitle: 'Provider'
        }
      case '/client/login':
        return {
          registerPath: '/client/register',
          headerTitle: 'Client'
        }
      case '/admin/login/forgot-password':
        return {
          forgotPasswordPath: '/admin/forgot-password',
          headerTitle: 'Admin'
        }
      case '/provider/login/forgot-password':
        return {
          forgotPasswordPath: '/provider/forgot-password',
          headerTitle: 'Provider'
        }
      case '/client/login/forgot-password':
        return {
          forgotPasswordPath: '/client/forgot-password',
          headerTitle: 'Client'
        }
      default:
        return null;

    }
  }

  getPortalType = () => {
    const pathName = this.props.location && this.props.location.pathname;
    switch (pathName) {
      case '/admin/login':
        return 'admin'
      case '/provider/login':
        return 'provider'
      case '/client/login':
        return 'client'
      default:
        return "";
    }
  }

  getSupportText = () => {
    const pathName = this.props.location && this.props.location.pathname;
    switch (pathName) {
      case '/provider/login':
        return "Please contact your admin or email support@myethera.com if you're having trouble signing in."
      case '/client/login':
        return "Please email support@myethera.com if you're having trouble signing in."
      default:
        return "";
    }
  }

  render() {
    const pathName = this.props.location && this.props.location.pathname;
    const { email, password } = this.state;
    return (
      <div className="login">
        <AuthHeader
          title=""
          lead={`${this.getConfigObject(pathName).headerTitle} Portal`}
        />
        <Container className="mt--8 pb-5">
          <Row className="justify-content-center">
            <Col lg="5" md="7">
              <Card className="bg-secondary border-0 mb-0" style={{ boxShadow: "none" }}>
                <CardBody className="px-lg-5 py-lg-5">
                  <Form role="form" onSubmit={this.login}>
                    <FormGroup
                      className={classnames("mb-3", {
                        focused: this.state.focusedEmail
                      })}
                    >
                      <InputGroup className="input-group-alternative">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="ni ni-email-83" />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          placeholder="Email"
                          type="email"
                          name="email"
                          onFocus={() => this.setState({ focusedEmail: true })}
                          onBlur={() => this.setState({ focusedEmail: false })}
                          onChange={this.handleChange}
                          maxLength="255"
                          autoFocus="True"
                        />
                      </InputGroup>
                    </FormGroup>
                    <FormGroup
                      className={classnames({
                        focused: this.state.focusedPassword
                      })}
                    >
                      <InputGroup className="input-group-alternative">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="ni ni-lock-circle-open" />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          placeholder="Password"
                          type="password"
                          name="password"
                          onFocus={() => this.setState({ focusedPassword: true })}
                          onBlur={() => this.setState({ focusedPassword: false })}
                          onChange={this.handleChange}
                          maxLength="255"
                        />
                      </InputGroup>
                    </FormGroup>
                    <div className="text-center">
                      <Button className="my-4 submit-button" color="info" type="submit" disabled={!email || !password}>
                        Sign In
                      </Button>
                    </div>
                    <div className="text-center forgot-password">
                      <Link
                        to={this.getConfigObject(`${pathName}/forgot-password`).forgotPasswordPath}
                      >
                        <small>Forgot your password?</small>
                      </Link>
                    </div>
                    <p className="support-text">{this.getSupportText()}</p>
                  </Form>
                </CardBody>
              </Card>
              <Row className="mt-3">
                <Col xs="6">
                  {/* <a
                    className="text-light"
                    href="#pablo"
                    onClick={e => e.preventDefault()}
                  >
                    <small>Forgot password?</small>
                  </a> */}
                </Col>
                {/* {
                  pathName !== '/admin/login' && pathName !== '/client/login' &&
                  <Col className="text-right" xs="6">
                    <Link
                      to={this.getConfigObject(pathName).registerPath}
                      className="text-light"
                      href="#pablo"
                    >
                      <small>Create new account</small>
                    </Link>
                  </Col>
                } */}
              </Row>
            </Col>
          </Row>
          <ServicesAndPolicy portal={this.getPortalType()} />
        </Container>
      </div>
    );
  }
}

const ServicesAndPolicy = ({ portal }) => {
  if (portal === "client") {
    return (
      <div className="login-footer">
        By using our platform, you acknowledge that you have read, understood, and agree to the <a target="_blank" href="https://client.myethera.com/tos">Terms of Service</a> and <a target="_blank" href="https://client.myethera.com/pp">Privacy Policy</a>.*
      </div>
    )
  } else if (portal === "provider") {
    return (
      <div className="login-footer">
        By using our platform, you acknowledge that you have read, understood, and agree to the <a target="_blank" href="https://myethera.com/baa">Business Associate Agreement</a>, the <a target="_blank" href="https://myethera.com/tos">Terms of Service</a>, the <a target="_blank" href="https://myethera.com/pp">Privacy Policy</a>, and the <a target="_blank" href="https://myethera.com/membership-policies">Membership and Office Services Agreements</a>*
      </div>
    )
  }
  return null
}

const mapStateToProps = state => {
  return {
    isAuthenticated: state.auth.isAuthenticated,
    haveError: state.auth.haveError,
  };
};

const mapActionToProps = dispatch => {
  return {
    login: data => dispatch({ type: AUTH_ACTIONS.LOGIN, data }),
  };
};

export default connect(
  mapStateToProps,
  mapActionToProps
)(Login);
